@import url('https://fonts.googleapis.com/css2?family=Kalnia+Glaze:wght@100..700&family=Roboto+Slab:wght@100..900&display=swap');

.App {
  font-family: sans-serif;
  text-align: center;
}

.App section {
  display: flex;
  justify-content: center;
  align-content: center;
}

.App section .wrapper {
  width: 100px;
}

.App section .wrapper h2 {
  margin-bottom: 10px;
}

.App section .wrapper h6 {
  margin: 0;
}

h1, h2 {
  margin-bottom: 0;
}

.title {
  font-family: "Kalnia Glaze", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  font-size: 1.5rem;
}

.numbers {
  font-family: "Roboto Slab", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  font-size: 1.5rem;
  color: deeppink;
}

img {
  margin-top: 2rem;
}